@media only screen and (max-width: 375px) {
  .category-box{
    padding: 66px!important;
  }
}

/* Extra small devices (phones, 675px and down) */
@media only screen and (max-width: 675px) {
  .ss-logo-splash{
    height: 55px!important;
    width: auto;
    margin-top: 195px;
  }

  .swap{
    vertical-align: 0.1;
    margin-top: -1px!important;
    height: 60px!important;
  }

  .ss-logo-text{
    font-size: 40px!important;
  }

  .intro-form-page{
    padding: 35px 8%!important;
  }

  .category-box{
    max-height: 130px;
    min-height: 130px;
    max-width: 130px;
    min-width: 130px;
    padding: 70px;
  }
  .category-name{
    font-weight: bold;
    font-size: 14px;
    color: white;
  }
  label img {
    height: 130px;
    width: 130px;
    transition-duration: 0.2s;
    transform-origin: 50% 50%;
    object-fit: cover;
  }
  .intro-form-container{
    height: 100%;
    width: 100%;
  }

  .intro-form-title{
    text-align: left!important;
  }

  .intro-form-desc{
    text-align: left!important;
  }

  .next-back-btn{
    border: none;
    background-color: transparent;
    color: #167D7F;
    font-size: 20px;
    float: right!important;
  }

  .homepage-container{
    margin-top: 80px!important;
  }

  .swiper{
    margin-top: 5px;
  }

  .conversation-container{
    margin-top: 80px!important;
  }

  .notifications-container{
    margin-top: 80px!important;
  }

  .item-img{
    object-fit: cover;
    object-position: center!important;
    height: 60px;
    width: 60px;
    border-radius: 10px;
    background-color: lightgray;
  }

  .select-item{
    margin-top: 2%!important;
  }

  .select-item-name{
    font-size: 14px!important;
  }

  .select-item-icon{
    font-size: 20px!important;
  }

  .select-item-icon > div{
    font-size: 10px!important;
    display: none!important;
  }

  .notification{
    max-height: auto!important;   
  }

  .notif-message{
    color: white;
    font-size: 14px;
    overflow: hidden; 
    text-overflow: ellipsis;
    display: -webkit-box;
   -webkit-line-clamp: 2; /* number of lines to show */
   -webkit-box-orient: vertical;
  }

  .chat-swap-img{
    height: 110px;
    width: 110px;
    border-radius: 10px;
    object-fit: cover;
    object-position: center;
  }
  
  .chat-swap-img img{
    height: 110px;
    width: 110px;
    border-radius: 10px;
    object-fit: cover;
    object-position: center;
  }
  
  .chat-swap-avatar img{
    height: 30px;
    width: 30px;
    border: 2px solid white;
  }

  
  .chat-swap-btn-ignore{
    background-color: white!important;
    border-radius: 10px!important;
    color: black;
    text-transform: capitalize;
    font-size: 14px!important;
    width: 100px;
    padding: 2%;
  }

  .chat-swap-btn-approve{
    background-color: #167D7F!important;
    border-radius: 10px!important;
    text-transform: capitalize;
    font-size: 14px!important;
    width: 100px;
    padding: 2%;
  }

  .conversation-search{
    margin-top: -30px;
  }

  img.profile-avatar{
    height: 70px;
    width: 70px;
    margin-right: 10px;
  }

  .profile-name{
    font-weight: bold;
    font-size: 18px;
  }

  .profile-address{
    font-size: 14px;
  }

  .profile-details{
    font-size: 16px;
    margin-top: 5px!important;
  }

  .item-gallery-container{
    height: 260px!important;
  }

  .profile-item-details{
    margin-top: 61px!important;
  }

  .profile-item-details-card{
    padding: 0%!important;
    box-shadow: none;
  }

  .item-img-car{
    height: 375px!important;
    width: 100%!important;
    max-width: 100%;
  }

  .settings-container{
    margin-top: 0px;
  }

  .edit-categories-container{
    margin-top: 80px;
  }

  .avatar-img-upload:before{
    font-size: 20px!important;
  }

  .marketplace-col{
    height: 200px;
    width: 150px;
    margin: 3px!important;
  }

  .marketplace-item-value{
    font-size: 16px;
  }

  .marketplace-item-details{
    font-size: 14px ;
  }

  .item-prev-upload {
    position: relative;
    cursor: pointer;
    border: 1px solid #167D7F;
    height: 125px!important;
    width: 125px!important;
  }
  /* .chat-screen-input{
    display: flex;
    padding: 5px;
    bottom: 0;
    width: 100%;
    bottom: 0px;
    position: fixed;
    border-top: 1px solid lightgray;
  } */
  
  .item-prev-upload-img:before{
    content: url('../assets/icons/upload.png');
    font-size: 130px;
    top: 45%;
    left: 50%;
    position: absolute;
    width: 125%;
    height: 125%;
    transform: translate(-50%, -45%);
    text-align: center;
    opacity: 0;
    transition: .5s ease;
    background-color: white;
  }

  .landing-carousel img{
    height: 100vh;
    width: auto!important;
  }

  .landing-carousel video{
    height: 100vh;
    width: auto!important;
  }

  .caption-content-img{
    height: 100%;
    display: none;
  }

  .caption-content{
    text-align: center;
  }

  .caption-content-title{
    font-size: 34px;
    margin-bottom: 5px;
  }
  
  .caption-content-desc{
    font-size: 16px;
    margin-bottom: 3px;
  }

  .landing-carousel-video{
    object-fit: cover!important;
    object-position: center;
    width: auto!important;
  }
  
  .section-1{
    margin: 5% 0% 3% 0%!important;
  }

  .landing-title{
    font-size: 50px;
  }

  .landing-title-desc{
    font-size: 34px;
  }

  .landing-desc{
    padding: 0% 5% 0% 5%!important;
  }

  .section-1{
    margin: 5% 5% 3% 5%!important;
  }

  .section-1-carousel{
    max-height: 170px;
    height: 170px;
    margin: 5% -5% 5% -5%!important;
  }

  .section-1-title{
    font-weight: bold;
    font-size: 28px;
    padding: 1% 5% 1% 5%;
  }
  
  .section-1-desc{
    font-size: 13px;
    padding: 0% 5% 2% 5%;
  }

  .section-1-btn-container{
    padding: 5%!important;
  }

  .section-1-btn{
    font-size: 18px;
  }

  .section-3{
    margin: 5% 5% 3% 5%!important;
  } 

  .section-4-desc{
    font-size: 20px;
    color: white!important;
    padding: 0% 10% 0% 10%;
  }

  .section-5{
    margin: 5% 5% 3% 5%!important;
  }

  .ss-footer-mask{
      padding: 100px 5%;
  }

  .ss-footer-title{
    font-size: 28px;
  }
  
  .footer-icon{
    background-color: #167D7F;
    font-size: 30px;
    border-radius: 50%;
    padding: 5%;
    height: 50px;
    width: 50px;
  }
  
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 990px) {

  .ss-btns-dsk{
    display: none;
  }

  .ss-btns-m{
    display: block;
  }

  @media only screen and (max-width: 992px) {
    .chat-screen-input{
      bottom: 1;
      width: 100%;
      position: relative;
    }

    .marketplace-swapcoins{
      color: #167D7F;
      font-weight: bold;
      font-size: 16px;
    }

    .marketplace-swapcoins i{
      margin-left: 14px;
    }

    .landing-navbar-link i{
      visibility: visible!important;
      margin: 2% 0%;
    }
  }

    .landing-carousel img{
      height: 100vh;
      width: auto!important;
  }
  
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {

}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (max-width: 992px) {
  .chat-screen-input{
    width: 100%;
    bottom: 1;
    position: relative;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 1050px) {
  .caption-content-img{
    height: 270px;
    width: 100%;
  }

  .caption-content-img img{
    height: 100%;
    width: auto;
  }

  .caption-content-title{
    font-size: 42px;
    margin-bottom: 5px;
  }
  
  .caption-content-desc{
    font-size: 20px;
    margin-bottom: 3px;
  }
}

@media only screen and (max-width: 1051px) and (min-width: 676px) {
  .caption-content-img{
    display: none;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1100px) {
  .profile-container{
    padding: 0px 15%;
  }
  .profile-tabs{
    padding: 0% 16%;
  }

  .profile-tab-content{
    padding: 0% 16%;
  }

}

@media only screen and (max-width: 455px) {
  .profile-tab-label{
    display: none;
  }
}